import gifshot from 'gifshot';
import b64toBlob from 'b64-to-blob';

function i18nMix(i18n) {
    i18n.update({
        'zh-cn': {
            'Long press, gif length is between 1 second and 10 seconds': 'gif  1 ~ 10 ',
            'Gif time is too short': 'Gif ',
            'Start creating gif...': ' gif...',
            'Create gif successfully': ' gif ',
            'There is another gif in the processing': ' gif ',
            'Release the mouse to start': '',
        },
        'zh-tw': {
            'Long press, gif length is between 1 second and 10 seconds': 'gif  1 ~ 10 ',
            'Gif time is too short': 'Gif ',
            'Start creating gif...': ' gif...',
            'Create gif successfully': ' gif ',
            'There is another gif in the processing': ' gif ',
            'Release the mouse to start': '',
        },
    });
}

function artplayerPluginGif(art) {
    const { errorHandle, clamp, downloadImage } = art.constructor.utils;
    const {
        i18n,
        notice,
        layers,
        controls,
        player,
        loading,
        option: { theme, title },
        events: { proxy },
        template: { $video },
    } = art;

    i18nMix(i18n);

    layers.add({
        name: 'artplayer-plugin-gif-progress',
        style: {
            position: 'absolute',
            top: '0',
            left: '0',
            height: '3px',
            width: '0%',
            'background-color': theme,
        },
    });

    const $progress = layers['artplayer-plugin-gif-progress'].$ref;
    const timeLimit = 10000;
    let isProcessing = false;
    let pressStartTime = 0;
    let progressTimer = null;
    let isPress = false;
    let offset = 0;

    function cleanTimer() {
        $progress.style.width = '0%';
        clearTimeout(progressTimer);
        progressTimer = null;
    }

    function createGif() {
        cleanTimer();
        const pressTime = new Date() - pressStartTime;
        if (isProcessing) {
            notice.show(i18n.get('There is another gif in the processing'));
        } else if (pressTime < 1000) {
            notice.show(i18n.get('Gif time is too short'));
        } else {
            const numFrames = Math.floor(clamp(pressTime, 1000, timeLimit) / 100);
            const { videoWidth, videoHeight } = $video;
            art.plugins.artplayerPluginGif.create(
                {
                    numFrames,
                    offset: Math.floor(offset),
                    gifHeight: 200,
                    gifWidth: (videoWidth / videoHeight) * 200,
                },
                image => {
                    downloadImage(image, `${title || 'unnamed'}.gif`);
                },
            );
        }
    }

    controls.add({
        name: 'artplayer-plugin-gif',
        position: 'right',
        html: 'GIF',
        mounted: $gif => {
            proxy($gif, 'mousedown', () => {
                isPress = true;
                cleanTimer();
                offset = player.currentTime;
                pressStartTime = new Date();
                notice.show(i18n.get('Long press, gif length is between 1 second and 10 seconds'));
                (function loop() {
                    progressTimer = setTimeout(() => {
                        const width = parseInt($progress.style.width, 10);
                        if (width <= 100) {
                            $progress.style.width = `${width + 1}%`;
                            loop();
                        } else {
                            notice.show(i18n.get('Release the mouse to start'));
                        }
                    }, timeLimit / 100);
                })();
            });

            proxy(document, 'mouseup', () => {
                if (isPress) {
                    isPress = false;
                    createGif();
                    offset = 0;
                }
            });
        },
    });

    return {
        name: 'artplayerPluginGif',
        create(config = {}, callback) {
            isProcessing = true;
            loading.show();
            art.emit('artplayerPluginGif:start');
            notice.show(i18n.get('Start creating gif...'), false);
            gifshot.createGIF(
                {
                    ...config,
                    video: [$video.src],
                    crossOrigin: 'anonymous',
                },
                obj => {
                    if (obj.error) {
                        notice.show(obj.errorMsg);
                        errorHandle(false, obj.errorMsg);
                    } else if (typeof callback === 'function') {
                        const base64String = obj.image.split(',')[1];
                        const blob = b64toBlob(base64String, 'image/gif');
                        const blobUrl = URL.createObjectURL(blob);
                        notice.show(i18n.get('Create gif successfully'));
                        art.emit('artplayerPluginGif', blobUrl);
                        callback(blobUrl);
                    }
                    isProcessing = false;
                    loading.hide();
                    art.emit('artplayerPluginGif:end');
                },
            );
        },
    };
}

window.artplayerPluginGif = artplayerPluginGif;
export default artplayerPluginGif;
